<template>
  <div>
    <base-header class="pb-6">
      <div class="row align-items-center py-4">
        <div class="col-lg-6 col-7">
          <h6 class="h2 text-white d-inline-block mb-0">Lesson Types</h6>
          <nav aria-label="breadcrumb" class="d-none d-md-inline-block ml-md-4">
            <route-breadcrumb />
          </nav>
        </div>
        <div class="col-lg-6 col-5 text-right">
          <!-- <base-button
            size="sm"
            type="neutral"
            @click.prevent="
              showPreview = false;
              showCreate = true;
              hasPackage = false;
            "
            class="tk-font-color-main"
            >Create</base-button
          >
          <base-button
            size="sm"
            type="neutral"
            v-if="showPreview || showCreate"
            @click.prevent="
              showPreview = false;
              showCreate = false;
              initDuration();
            "
            class="tk-font-color-main"
            >Collapse</base-button
          > -->
        </div>
      </div>
    </base-header>
    <div class="container-fluid mt--6">
      <div class="row">
        <div
          class="col-lg-4 card tk-px-margin-bottom-px0"
          style="height: calc(100vh - 90px)"
        >
          <card-loading v-if="loading"></card-loading>
          <template v-else>
            <div class="card-header" style="padding: 1rem 0">
              <div class="row align-items-center">
                <div class="col-12 text-right">
                  <base-button
                    size="sm"
                    type="neutral"
                    @click.prevent="
                      showPreview = false;
                      showCreate = true;
                      hasPackage = false;
                      clearSelected();
                    "
                    class="tk-font-color-main"
                    >Create</base-button
                  >
                </div>
              </div>
            </div>
            <div class="card-body tk-px-padding-px0">
              <inline-actions-table
                class="tk-table-padding"
                :lessonTypes="lessonTypes"
                @preview="preview"
              ></inline-actions-table>
            </div>
          </template>
        </div>
        <div class="col-lg-8 card-wrapper tk-position-relative">
          <img
            src="/img/icons/tk/img_type.png"
            class="tk-px-margin-top-px80 tk-position-absolute"
            style="
              width: calc(100% - 80px);
              left: 50%;
              transform: translateX(-50%);
            "
          />
          <div class="row">
            <div class="col-lg-7 card-wrapper" v-if="showPreview || showCreate">
              <div class="card">
                <div class="card-header" style="padding: 1rem">
                  <div class="row align-items-center">
                    <div class="col-6">
                      <h5 class="h3 mb-0" v-if="showPreview">
                        Lesson Type Detail
                      </h5>
                      <h5 class="h3 mb-0" v-else>New Lesson Type</h5>
                    </div>
                    <div class="col-6 text-right">
                      <span
                        class="btn btn-sm btn-danger tk-bg-color-red tk-font-color-white tk-border-color-red"
                        v-if="showPreview"
                        @click.prevent="deleteLessonType"
                        >Delete</span
                      >
                      <button-with-loading
                        style="margin: 0 1.5rem 0 0; display: inline-block"
                        type="main"
                        size="40"
                        fontSize="10"
                        padding="5px 8px"
                        text="Save"
                        @start="saveLessonType"
                        :complete="isButtonLoadingComplete"
                      ></button-with-loading>
                      <span
                        class="tk-font-color-main tk-cursor-pointer"
                        style="margin-right:12px"
                        @click.prevent="
                          showPreview = false;
                          showCreate = false;
                          initDuration();
                        "
                      >
                        <i class="fas fa-times"></i>
                      </span>
                    </div>
                  </div>
                </div>
                <div class="card-body" style="padding: 1rem">
                  <!-- name -->
                  <span>
                    <div
                      class="form-group card-wrapper tk-px-margin-bottom-px0"
                    >
                      <label class="form-control-label"
                        >Name of lesson type</label
                      >
                      <div class="card tk-px-margin-bottom-px16">
                        <div class="has-label card-body tk-px-padding-px0">
                          <input
                            type="text"
                            class="form-control tk-font-color-black1"
                            v-model="previewData.name"
                            v-if="showPreview"
                          />
                          <input
                            type="text"
                            class="form-control tk-font-color-black1"
                            v-model="createData.name"
                            v-else
                          />
                        </div>
                      </div>
                    </div>
                  </span>
                  <!-- instrument -->
                  <span>
                    <div
                      class="form-group card-wrapper tk-px-margin-bottom-px0"
                    >
                      <div
                        class="card tk-border-color-gray-300 tk-px-margin-bottom-px16"
                      >
                        <div class="card-body tk-px-padding-px16">
                          <div
                            class="avatar rounded-circle tk-layout-inline-block tk-px-margin-right-px10 tk-bg-color-gray-200 tk-border-color-gray-200"
                          >
                            <img
                              v-if="showPreview"
                              :src="getInstrumentIcon(previewData)"
                            />
                            <img v-else :src="getInstrumentIcon(createData)" />
                          </div>
                          <div
                            class="tk-layout-inline-block tk-font-color-main tk-text-right tk-cursor-pointer"
                            style="width: calc(100% - 64px)"
                            @click.prevent="showInstruments = !showInstruments"
                          >
                            <span class="tk-layout-inline-block text-xs"
                              >Select an icon</span
                            >
                            <i
                              class="fas fa-angle-down tk-layout-inline-block tk-px-margin-left-px8"
                            ></i>
                          </div>
                          <div v-if="showInstruments" class="tk-text-center">
                            <template v-for="(item, index) in instruments">
                              <div
                                :key="index"
                                class="tk-layout-inline-block tk-px-margin-top-px16 tk-px-margin-right-px16 tk-cursor-pointer"
                                @click.prevent="
                                  previewData.instrumentId = item.id.toString();
                                  createData.instrumentId = item.id.toString();
                                  showInstruments = false;
                                "
                              >
                                <div
                                  class="avatar avatar-lg rounded-circle tk-bg-color-gray-200 tk-border-color-gray-200"
                                >
                                  <img :src="item.url" />
                                </div>
                                <div class="tk-font-color-lightGray text-xs">
                                  {{ item.name }}
                                </div>
                              </div>
                            </template>
                          </div>
                        </div>
                      </div>
                    </div>
                  </span>
                  <!-- duration -->
                  <span>
                    <div
                      class="form-group card-wrapper tk-px-margin-bottom-px0"
                    >
                      <label class="form-control-label">Duration(min)</label>
                      <div
                        class="card tk-border-color-gray-300 tk-px-margin-bottom-px16"
                      >
                        <div class="has-label card-body tk-px-padding-px16">
                          <template v-for="(item, index) in durations">
                            <div
                              class="tk-layout-inline-block tk-px-radius-px5 tk-px-padding-top-px10 tk-px-margin-right-px10 tk-px-padding-bottom-px10 tk-text-center tk-cursor-pointer"
                              :class="{
                                'tk-border-color-main tk-font-color-main':
                                  item.selected,
                                'tk-border-color-gray-300 tk-font-color-gray': !item.selected,
                                'tk-px-margin-bottom-px10': index < 4,
                              }"
                              :key="index"
                              style="width: calc(25% - 10px)"
                              @click.prevent="selectDuration(item)"
                            >
                              {{ item.length }}
                            </div>
                          </template>
                        </div>
                      </div>
                    </div>
                  </span>
                  <!-- package -->
                  <span>
                    <div
                      class="form-group card-wrapper tk-px-margin-bottom-px0"
                    >
                      <div
                        class="card tk-border-color-gray-300 tk-px-margin-bottom-px16"
                      >
                        <div class="card-body tk-px-padding-px16">
                          <span
                            class="tk-layout-inline-block tk-font-color-black1"
                            style="width: calc(100% - 56px)"
                            >Package option</span
                          >
                          <base-switch
                            class="mr-1 tk-layout-inline-block"
                            v-model="hasPackage"
                            on-text="On"
                            off-text="Off"
                            type="main"
                          ></base-switch>
                          <div
                            class="invalid-feedback tk-font-color-gray tk-px-margin-top-px10"
                            style="display: block"
                            v-if="
                              previewData.package > 0 || createData.package > 0
                            "
                          >
                            <base-button
                              style="
                                padding: 2px 5px;
                                background-color: rgba(176, 238, 211, 0.48);
                                box-shadow: none;
                                z-index: 1;
                              "
                              class="tk-font-color-green tk-position-relative tk-border-none tk-px-margin-right-px10"
                              @click.prevent="
                                showPackageList = !showPackageList
                              "
                            >
                              <span v-if="showPreview"
                                >{{ previewData.package }} lesson(s)</span
                              >
                              <span v-if="showCreate"
                                >{{ createData.package }} lesson(s)</span
                              >
                              <i
                                class="fas fa-caret-down tk-px-margin-right-px0"
                              ></i>
                              <item-list
                                :data="packageList"
                                :value="createData.package"
                                v-if="showPackageList"
                                class="tk-position-absolute"
                                @select="select"
                              ></item-list>
                            </base-button>
                            <span>required</span>
                          </div>
                        </div>
                      </div>
                    </div>
                  </span>
                  <!-- price -->
                  <span>
                    <div
                      class="form-group card-wrapper tk-px-margin-bottom-px0"
                    >
                      <label class="form-control-label">Price / lesson</label>
                      <div class="card tk-px-margin-bottom-px8">
                        <div class="has-label card-body tk-px-padding-px0">
                          <input
                            v-if="showPreview"
                            type="text"
                            class="form-control tk-font-color-black1"
                            @keypress.enter.prevent=""
                            v-model="previewData.price"
                          />
                          <input
                            v-else
                            type="text"
                            class="form-control tk-font-color-black1"
                            @keypress.enter.prevent=""
                            v-model="createData.price"
                          />
                        </div>
                      </div>
                      <div
                        class="invalid-feedback tk-font-color-lightGray"
                        style="display: block"
                      >
                        Invisible for students
                      </div>
                    </div>
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <modal :show.sync="showDelete">
      <template slot="header">
        <h5 class="modal-title" id="exampleModalLabel">Delete Lesson Type</h5>
      </template>
      <div class="tk-popup-content">
        <template v-if="lessons.length > 0">
          You still have
          <strong class="tk-font-color-black1"
            >{{ lessons.length }} lesson(s)</strong
          >
          under it.
          <br />
        </template>
        Do you want to delete the lesson type
        <strong class="tk-font-color-black1">[{{ previewData.name }}]</strong> ?
      </div>
      <template slot="footer">
        <base-button
          type="secondary"
          @click.prevent="showDelete = false"
          class="tk-font-color-lightGray tk-border-none"
          >Close</base-button
        >
        <!-- <base-button
          type="primary"
          @click.prevent="confirmDelete"
          class="tk-font-color-white tk-bg-color-red tk-border-none"
          >Delete</base-button
        > -->
        <button-with-loading
          style="margin: 0"
          type="red"
          text="Delete"
          color="#fa897b"
          @start="confirmDelete"
          :complete="isButtonLoadingComplete"
        ></button-with-loading>
      </template>
    </modal>
  </div>
</template>

<script>
import bus from "@/bus.js";
import query from "@/services/query.js";
import commons from "@/util/commons.js";
import collections from "@/services/collections.js";
import userService from "@/services/userService.js";
export default {
  components: {
    InlineActionsTable: () =>
      import("./LessonTypes/RegularTables/InlineActionsTable"),
    ItemList: () => import("../components/layout/ItemList.vue"),
    cardLoading: () => import("@/tkViews/components/layout/CardLoading"),
    buttonWithLoading: () =>
      import("@/tkViews/components/layout/ButtonWithLoading"),
  },
  data() {
    return {
      isButtonLoadingComplete: false,
      showDelete: false,
      showPreview: false,
      showCreate: false,
      showPackageList: false,
      previewData: {},
      createData: {
        id: "",
        teacherId: "",
        instrumentId: "",
        timeLength: "",
        price: "",
        type: 1,
        name: "",
        deleted: false,
        package: 0,
        createTime: "",
        updateTime: "",
      },
      hasPackage: false,
      showInstruments: false,
      instruments: [],
      durations: [],
      lessonTypes: [],
      packageList: [],
      lessons: [],

      loading: true,
    };
  },
  watch: {
    hasPackage(newVal, oldVal) {
      if (newVal) {
        this.previewData.package = 5;
        this.createData.package = 5;
      } else {
        this.previewData.package = 0;
        this.createData.package = 0;
      }
    },
  },
  created() {
    let self = this;
    self.instruments = self.$tools.instruments;
    self.durations = self.$tools.durations;

    for (let i = 0; i < 50; i++) {
      self.packageList.push({
        label: i > 0 ? i + 1 + " lessons" : i + 1 + " lesson",
        value: i + 1,
      });
    }

    self.getLessonTypes();
  },
  methods: {
    clearSelected() {
      bus.$emit("clearSelected");
    },
    getInstrumentIcon(row) {
      let url = "";
      this.instruments.some((item) => {
        if (parseInt(item.id) === parseInt(row.instrumentId)) {
          url = item.url;
        }
      });
      return url;
    },
    async getLessonTypes() {
      this.lessonTypes = await query.listAllLessonTypesInStudio();
      this.loading = false;
    },
    select(item) {
      let self = this;
      self.previewData.package = item.value;
      self.createData.package = item.value;
    },
    preview(data) {
      let self = this;
      self.showPreview = true;
      self.showCreate = false;
      self.previewData = data;
      self.hasPackage = data.package > 0;
      self.durations.some((item) => {
        item.selected = false;
        if (parseInt(item.length) === parseInt(self.previewData.timeLength)) {
          item.selected = true;
        }
      });
      self.lessons = [];
    },
    selectDuration(item) {
      let self = this;
      self.durations.some((time) => {
        time.selected = false;
      });
      item.selected = true;
      self.previewData.duration = item.length;
      self.createData.duration = item.length;
    },
    initDuration() {
      this.durations.some((item) => {
        item.selected = false;
      });
      this.initCreateData();
    },
    initCreateData() {
      this.createData = {
        id: "",
        teacherId: "",
        instrumentId: "",
        timeLength: "",
        price: "",
        type: 1,
        name: "",
        deleted: false,
        package: 0,
        createTime: "",
        updateTime: "",
      };
    },
    isNull(data) {
      return this.$tools.isNull(data);
    },
    async deleteLessonType() {
      let self = this;
      self.showDelete = true;
      self.lessons = [];
      self.lessons = await query.getLessonsByLessonTypeId(self.previewData.id);
      console.log("lessons: ", self.lessons);
    },
    async confirmDelete() {
      let self = this;
      self.previewData.deleted = true;
      await collections.lessonType
        .doc(self.previewData.id)
        .update(self.previewData)
        .then(() => {
          self.isButtonLoadingComplete = true;
          self.$notify({
            message: "Delete Success!",
            type: "success",
          });
        })
        .catch((err) => {
          console.log("Delete failed, error: ", err);
          self.$notify({
            message: "Delete failed, try again!",
            type: "error",
          });
        });
      self.isButtonLoadingComplete = false;
      self.showDelete = false;
      self.getLessonTypes();
      self.showPreview = false;
    },
    async saveLessonType() {
      let self = this;
      if (self.showPreview) {
        self.previewData.updateTime = parseInt(new Date() / 1000).toString();
        self.previewData.price = parseInt(self.previewData.price).toString();
        await collections.lessonType
          .doc(self.previewData.id)
          .update(self.previewData)
          .then(() => {
            self.isButtonLoadingComplete = true;
            self.$notify({
              message: "Update Success!",
              type: "success",
            });
          })
          .catch((err) => {
            console.log("Update failed, error: ", err);
            self.$notify({
              message: "Update failed, try again!",
              type: "error",
            });
          });
      }

      if (self.showCreate) {
        console.log(self.createData);
        let now = parseInt(new Date().getTime() / 1000).toString();
        self.createData.createTime = now;
        self.createData.updateTime = now;
        self.createData.id = commons.generateId();
        self.createData.teacherId = commons.userId();
        self.createData.price = parseInt(self.createData.price).toString();
        self.durations.some((item) => {
          if (item.selected) {
            self.createData.timeLength = parseInt(item.length);
          }
        });

        await collections.lessonType
          .doc(self.createData.id)
          .set(self.createData)
          .then(() => {
            self.isButtonLoadingComplete = true;
            self.$notify({
              message: "Create Success!",
              type: "success",
            });
            self.clearSelected();
            self.preview(self.createData);
          })
          .catch((err) => {
            console.log("Create failed, error: ", err);
            self.$notify({
              message: "Create failed, try again!",
              type: "error",
            });
          });
      }
      self.isButtonLoadingComplete = false;
      self.getLessonTypes();
    },
  },
};
</script>